	module.exports = {"data":{"postsJson":{"title":"Third Missionary Journey","questionsTitle":"OPENING THE WORD","display":"block","bookName":"Acts","ref":"18:23-21:16","html":"<p>   <strong>18</strong><u>23</u>Having spent some time there, he departed, and went through the region of Galatia, and Phrygia, in order, establishing all the disciples. <u>24</u>Now a certain Jew named Apollos, an Alexandrian by race, an eloquent man, came to Ephesus. He was mighty in the Scriptures. <u>25</u>This man had been instructed in the way of the Lord; and being fervent in spirit, he spoke and taught accurately the things concerning Jesus, although he knew only the baptism of John. <u>26</u>He began to speak boldly in the synagogue. But when Priscilla and Aquila heard him, they took him aside, and explained to him the way of God more accurately.<br />   <u>27</u>When he had determined to pass over into Achaia, the brothers encouraged him, and wrote to the disciples to receive him. When he had come, he greatly helped those who had believed through grace; <u>28</u>for he powerfully refuted the Jews, publicly showing by the Scriptures that Jesus was the Christ.<br />   <strong>19</strong><u>1</u>While Apollos was at Corinth, Paul, having passed through the upper country, came to Ephesus, and found certain disciples. <u>2</u>He said to them,</p><h1>PAUL</h1><h3>Did you receive the Holy Spirit when you believed?</h3><p>   They said to him,</p><h1>BELIEVERS IN EPHESUS</h1><h4>No, we haven’t even heard that there is a Holy Spirit.</h4><p>   <u>3</u>He said,</p><h1>PAUL</h1><h3>Into what then were you baptized?</h3><p>   They said,</p><h1>BELIEVERS IN EPHESUS</h1><h4>Into John’s baptism.</h4><p>   <u>4</u>Paul said,</p><h1>PAUL</h1><h3>John indeed baptized with the baptism of repentance, saying to the people that they should believe in the one who would come after him, that is, in Jesus.</h3><p>   <u>5</u>When they heard this, they were baptized in the name of the Lord Jesus. <u>6</u>When Paul had laid his hands on them, the Holy Spirit came on them, and they spoke with other languages and prophesied. <u>7</u>They were about twelve men in all. <u>8</u>He entered into the synagogue, and spoke boldly for a period of three months, reasoning and persuading about the things concerning God’s Kingdom.<br />   <u>9</u>But when some were hardened and disobedient, speaking evil of the Way before the multitude, he departed from them, and separated the disciples, reasoning daily in the school of Tyrannus. <u>10</u>This continued for two years, so that all those who lived in Asia heard the word of the Lord Jesus, both Jews and Greeks.<br />   <u>11</u>God worked special miracles by the hands of Paul, <u>12</u>so that even handkerchiefs or aprons were carried away from his body to the sick, and the evil spirits went out. <u>13</u>But some of the itinerant Jews, exorcists, took on themselves to invoke over those who had the evil spirits the name of the Lord Jesus, saying,</p><h1>SEVEN SONS OF SCEVA</h1><h4>We adjure you by Jesus whom Paul preaches.</h4><p><u>14</u>There were seven sons of one Sceva, a Jewish chief priest, who did this.<br />   <u>15</u>The evil spirit answered,</p><h1>A DEMON</h1><h4>Jesus I know, and Paul I know, but who are you?</h4><p><u>16</u>The man in whom the evil spirit was leaped on them, and overpowered them, and prevailed against them, so that they fled out of that house naked and wounded. <u>17</u>This became known to all, both Jews and Greeks, who lived at Ephesus. Fear fell on them all, and the name of the Lord Jesus was magnified. <u>18</u>Many also of those who had believed came, confessing, and declaring their deeds. <u>19</u>Many of those who practiced magical arts brought their books together and burned them in the sight of all. They counted their price, and found it to be fifty thousand pieces of silver.* <u>20</u>So the word of the Lord was growing and becoming mighty.<br />   <u>21</u>Now after these things had ended, Paul determined in the spirit, when he had passed through Macedonia and Achaia, to go to Jerusalem, saying,</p><h1>PAUL</h1><h3>After I have been there, I must also see Rome.</h3><p>   <u>22</u>Having sent into Macedonia two of those who served him, Timothy and Erastus, he himself stayed in Asia for a while. <u>23</u>About that time there arose no small stir concerning the Way. <u>24</u>For a certain man named Demetrius, a silversmith, who made silver shrines of Artemis, brought no little business to the craftsmen, <u>25</u>whom he gathered together, with the workmen of like occupation, and said,</p><h1>DEMETRIUS OF EPHESUS</h1><h4>Sirs, you know that by this business we have our wealth. <u>26</u>You see and hear, that not at Ephesus alone, but almost throughout all Asia, this Paul has persuaded and turned away many people, saying that they are no gods, that are made with hands. <u>27</u>Not only is there danger that this our trade come into disrepute, but also that the temple of the great goddess Artemis will be counted as nothing, and her majesty destroyed, whom all Asia and the world worships.</h4><p>   <u>28</u>When they heard this they were filled with anger, and cried out, saying,</p><h1>THE CRAFTSMEN OF EPHESUS</h1><h4>Great is Artemis of the Ephesians!</h4><p><u>29</u>The whole city was filled with confusion, and they rushed with one accord into the theater, having seized Gaius and Aristarchus, men of Macedonia, Paul’s companions in travel. <u>30</u>When Paul wanted to enter in to the people, the disciples didn’t allow him. <u>31</u>Certain also of the Asiarchs, being his friends, sent to him and begged him not to venture into the theater. <u>32</u>Some therefore cried one thing, and some another, for the assembly was in confusion. Most of them didn’t know why they had come together. <u>33</u>They brought Alexander out of the multitude, the Jews putting him forward. Alexander beckoned with his hand, and would have made a defense to the people. <u>34</u>But when they perceived that he was a Jew, all with one voice for a time of about two hours cried out,</p><h1>THE PEOPLE OF EPHESUS</h1><h4>Great is Artemis of the Ephesians!</h4><p>   <u>35</u>When the town clerk had quieted the multitude, he said,</p><h1>THE MAYOR OF EPHESUS</h1><h4>You men of Ephesus, what man is there who doesn’t know that the city of the Ephesians is temple keeper of the great goddess Artemis, and of the image which fell down from Zeus? <u>36</u>Seeing then that these things can’t be denied, you ought to be quiet, and to do nothing rash. <u>37</u>For you have brought these men here, who are neither robbers of temples nor blasphemers of your goddess. <u>38</u>If therefore Demetrius and the craftsmen who are with him have a matter against anyone, the courts are open, and there are proconsuls. Let them press charges against one another. <u>39</u>But if you seek anything about other matters, it will be settled in the regular assembly. <u>40</u>For indeed we are in danger of being accused concerning today’s riot, there being no cause. Concerning it, we wouldn’t be able to give an account of this commotion.</h4><p><u>41</u>When he had thus spoken, he dismissed the assembly.<br />   <strong>20</strong><u>1</u>After the uproar had ceased, Paul sent for the disciples, took leave of them, and departed to go into Macedonia. <u>2</u>When he had gone through those parts, and had encouraged them with many words, he came into Greece. <u>3</u>When he had spent three months there, and a plot was made against him by Jews as he was about to set sail for Syria, he determined to return through Macedonia. <u>4</u>These accompanied him as far as Asia: Sopater of Beroea; Aristarchus and Secundus of the Thessalonians; Gaius of Derbe; Timothy; and Tychicus and Trophimus of Asia. <u>5</u>But these had gone ahead, and were waiting for us at Troas. <u>6</u>We sailed away from Philippi after the days of Unleavened Bread, and came to them at Troas in five days, where we stayed seven days.<br />   <u>7</u>On the first day of the week, when the disciples were gathered together to break bread, Paul talked with them, intending to depart on the next day, and continued his speech until midnight. <u>8</u>There were many lights in the upper room where we* were gathered together. <u>9</u>A certain young man named Eutychus sat in the window, weighed down with deep sleep. As Paul spoke still longer, being weighed down by his sleep, he fell down from the third floor, and was taken up dead. <u>10</u>Paul went down, and fell upon him, and embracing him said,</p><h1>PAUL</h1><h3>Don’t be troubled, for his life is in him.</h3><p>   <u>11</u>When he had gone up, and had broken bread, and eaten, and had talked with them a long while, even until break of day, he departed. <u>12</u>They brought the boy in alive, and were greatly comforted.<br />   <u>13</u>But we who went ahead to the ship set sail for Assos, intending to take Paul aboard there, for he had so arranged, intending himself to go by land. <u>14</u>When he met us at Assos, we took him aboard, and came to Mitylene. <u>15</u>Sailing from there, we came the following day opposite Chios. The next day we touched at Samos and stayed at Trogyllium, and the day after we came to Miletus. <u>16</u>For Paul had determined to sail past Ephesus, that he might not have to spend time in Asia; for he was hastening, if it were possible for him, to be in Jerusalem on the day of Pentecost.<br />   <u>17</u>From Miletus he sent to Ephesus, and called to himself the elders of the assembly. <u>18</u>When they had come to him, he said to them,</p><h1>PAUL</h1><h3>You yourselves know, from the first day that I set foot in Asia, how I was with you all the time, <u>19</u>serving the Lord with all humility, with many tears, and with trials which happened to me by the plots of the Jews; <u>20</u>how I didn’t shrink from declaring to you anything that was profitable, teaching you publicly and from house to house, <u>21</u>testifying both to Jews and to Greeks repentance toward God, and faith toward our Lord Jesus.* <u>22</u>Now, behold, I go bound by the Spirit to Jerusalem, not knowing what will happen to me there; <u>23</u>except that the Holy Spirit testifies in every city, saying that bonds and afflictions wait for me. <u>24</u>But these things don’t count; nor do I hold my life dear to myself, so that I may finish my race with joy, and the ministry which I received from the Lord Jesus, to fully testify to the Good News of the grace of God.<br />   <u>25</u>Now, behold, I know that you all, among whom I went about preaching God’s Kingdom, will see my face no more. <u>26</u>Therefore I testify to you today that I am clean from the blood of all men, <u>27</u>for I didn’t shrink from declaring to you the whole counsel of God. <u>28</u>Take heed, therefore, to yourselves, and to all the flock, in which the Holy Spirit has made you overseers, to shepherd the assembly of the Lord and* God which he purchased with his own blood. <u>29</u>For I know that after my departure, vicious wolves will enter in among you, not sparing the flock. <u>30</u>Men will arise from among your own selves, speaking perverse things, to draw away the disciples after them. <u>31</u>Therefore watch, remembering that for a period of three years I didn’t cease to admonish everyone night and day with tears. <u>32</u>Now, brothers,* I entrust you to God, and to the word of his grace, which is able to build up, and to give you the inheritance among all those who are sanctified. <u>33</u>I coveted no one’s silver, or gold, or clothing. <u>34</u>You yourselves know that these hands served my necessities, and those who were with me. <u>35</u>In all things I gave you an example, that so laboring you ought to help the weak, and to remember the words of the Lord Jesus, that he himself said, ‘It is more blessed to give than to receive.’</h3><p>   <u>36</u>When he had spoken these things, he knelt down and prayed with them all. <u>37</u>They all wept a lot, and fell on Paul’s neck and kissed him, <u>38</u>sorrowing most of all because of the word which he had spoken, that they should see his face no more. And they accompanied him to the ship.<br />   <strong>21</strong><u>1</u>When we had departed from them and had set sail, we came with a straight course to Cos, and the next day to Rhodes, and from there to Patara. <u>2</u>Having found a ship crossing over to Phoenicia, we went aboard, and set sail. <u>3</u>When we had come in sight of Cyprus, leaving it on the left hand, we sailed to Syria, and landed at Tyre, for there the ship was to unload her cargo. <u>4</u>Having found disciples, we stayed there seven days. These said to Paul through the Spirit, that he should not go up to Jerusalem. <u>5</u>When those days were over, we departed and went on our journey. They all, with wives and children, brought us on our way until we were out of the city. Kneeling down on the beach, we prayed. <u>6</u>After saying goodbye to each other, we went on board the ship, and they returned home again.<br />   <u>7</u>When we had finished the voyage from Tyre, we arrived at Ptolemais. We greeted the brothers, and stayed with them one day. <u>8</u>On the next day, we, who were Paul’s companions, departed, and came to Caesarea.<br />   We entered into the house of Philip the evangelist, who was one of the seven, and stayed with him. <u>9</u>Now this man had four virgin daughters who prophesied. <u>10</u>As we stayed there some days, a certain prophet named Agabus came down from Judea. <u>11</u>Coming to us, and taking Paul’s belt, he bound his own feet and hands, and said,</p><h1>AGABUS THE PROPHET</h1><h4>Thus says the Holy Spirit: ‘So will the Jews at Jerusalem bind the man who owns this belt, and will deliver him into the hands of the Gentiles.’</h4><p>   <u>12</u>When we heard these things, both we and they of that place begged him not to go up to Jerusalem. <u>13</u>Then Paul answered,</p><h1>PAUL</h1><h3>What are you doing, weeping and breaking my heart? For I am ready not only to be bound, but also to die at Jerusalem for the name of the Lord Jesus.</h3><p>   <u>14</u>When he would not be persuaded, we ceased, saying,</p><h1>BELIEVERS IN CAESAREA</h1><h4>The Lord’s will be done.</h4><p>   <u>15</u>After these days we took up our baggage and went up to Jerusalem. <u>16</u>Some of the disciples from Caesarea also went with us, bringing one Mnason of Cyprus, an early disciple, with whom we would stay.</p><h5>*Acts 19:19 The 50,000 pieces of silver here probably referred to 50,000 drachmas. If so, the value of the burned books was equivalent to about 160 man-years of wages for agricultural laborers<br />*Acts 20:8 TR reads “they” instead of “we”<br />*Acts 20:21 TR adds “Christ”<br />*Acts 20:28 TR, NU omit “the Lord and”<br />*Acts 20:32 The word for “brothers” here and where the context allows may also be correctly translated “brothers and sisters” or “siblings.”</h5>","path":"/360","Question1":"What stands out to you in this passage?","Question2":"What truths do you gain about the character of God?","Question3":"What truths do you gain about men and women?","Question4":"What changes are you motivated to make in light of this passage?","prevPath":"/359","nextPath":"/361"}},"pathContext":{}}

